import React from 'react'
import home from '../../Components/Media/home.png'
import water from '../../Components/Media/water.png'
import team from '../../Components/Media/team.png'
import {useNavigate} from 'react-router-dom'


function ModuleList() {
    const navigate = useNavigate()

    return (
        <>
        {/* ==========MODULES========= */}
        <div className="grid grid-cols-12 gap-2 mt-4 gap-x-6 mt-20  md:mt-24 pr-10">
          
            <div onClick={()=>navigate('/propertyDashboard')} className={` rounded overflow-hidden shadow-lg relative bg-sky-700 cursor-pointer hover:scale-105 transition-all duration-500 col-span-5 md:col-span-4 md:py-2 text-white`}>
                <img className={`w-10 md:w-16 absolute bottom-0 right-0`} src={home} />
                <div className="px-6 py-4">
                    <div className="font-bold text-xl">Property</div>
                    <p className="text-white text-sm w-3/5 hidden md:block">
                        Choose for property option
                    </p>
                </div>
            </div>
            <div onClick={()=>navigate('/water')} className={` rounded overflow-hidden shadow-lg relative bg-sky-700 cursor-pointer hover:scale-105 transition-all duration-500 col-span-5 md:col-span-4 py-2 text-white`} >
                <img className={`w-10 md:w-16 absolute bottom-0 right-5`} src={water} />
                <div className="px-6 py-4 ">
                    <div className="font-bold text-xl">Water</div>
                    <p className="text-white text-sm w-3/5 hidden md:block">
                    Choose for water option
                    </p>
                </div>
            </div>
            <div onClick={()=>navigate('/trade')} className={` rounded overflow-hidden shadow-lg relative bg-sky-700 cursor-pointer hover:scale-105 transition-all duration-500 col-span-5 md:col-span-4 py-2 text-white`} >
                <img className={`w-10 md:w-16 absolute bottom-0 right-5`} src={team} />
                <div className="px-6 py-4">
                    <div className="font-bold text-xl">Trade</div>
                    <p className="text-white text-sm w-3/5 hidden md:block">
                    Choose for Trade option
                    </p>
                </div>
            </div>
            <div className="w-full col-span-12 text-right mt-5 font-semibold">
               <span className='px-4 py-2 hover:bg-gray-400 cursor-pointer rounded-lg hover:text-white'>{'More >>'}</span>
            </div>
           
        </div>

        </>
    )
}

export default ModuleList