import { useEffect, useState } from 'react'
import axios from 'axios';
import ApiHeader from '../../Components/ApiList/ApiHeader';
import CitizenApplyApiList from '../../Components/CitizenApplyApiList';
import { ColorRing } from "react-loader-spinner";
import BarLoader from '../../Components/Common/BarLoader';


function NotificationCard(props) {
    const { api_getAllApplicationList } = CitizenApplyApiList()
    const [loaderStatus, setloaderStatus] = useState(false)
    const [countData, setcountData] = useState('')


    useEffect(() => {
        fetchApplicationList();
    }, [])

    const fetchApplicationList = () => {
        setloaderStatus(true)
        axios.get(api_getAllApplicationList, ApiHeader())
            .then(function (response) {
                console.log("count response", response?.data?.data);
                setcountData(response?.data?.data)
                setloaderStatus(false)
                if(props?.isDashboard=='yes'){
                    props?.counterDataFun(response?.data?.data)
                }

            })
            .catch(function (error) {
                console.log('count error....', error);
                setloaderStatus(false)
            })

    }
    return (
        <div className='bg-blue-50'>
            {loaderStatus && (
                <BarLoader/>
            )}
            <div className='p-2'>
                <div className='mb-2 text-gray-700 font-sans font-semibold'>Notifications</div>
                <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2'>Property appication no 2083 has been approved</div>
                <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2'>Property appication no 2083 has been approved</div>
                <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2'>Property appication no 2083 has been approved</div>
            </div>
            <div className='w-full h-1 border-b-2 border-white mt-10'></div>
            <div className='p-2'>
                <div className='mb-2 text-gray-700 font-sans font-semibold'>Actions</div>
                <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2'>Pay your property tax <span className='ml-4 cursor-pointer bg-sky-400 text-white px-2 py-1 text-xs shadow-lg'>Pay</span></div>
                <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2'>Pay your property tax <span className='ml-4 cursor-pointer bg-sky-400 text-white px-2 py-1 text-xs shadow-lg'>Pay</span></div>
                <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2'>Pay your property tax <span className='ml-4 cursor-pointer bg-sky-400 text-white px-2 py-1 text-xs shadow-lg'>Pay</span></div>
            </div>
            <div className='w-full h-1 border-b-2 border-white mt-10'></div>
            <div className='p-2'>
                <div className='text-gray-700 font-sans font-semibold'>Summary</div>
                {countData != ' ' && <>
                    <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center w-3/4 rounded-sm'>Total Property<span className='ml-4 bg-yellow-400 text-white px-2 py-1 text-xs shadow-lg rounded-lg '>{countData?.Holding?.totalApplications}</span></div>
                    <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2 w-3/4 rounded-sm'>Total SAF Applied<span className='ml-4 bg-yellow-400 text-white px-2 py-1 text-xs shadow-lg rounded-lg '>{countData?.Property?.SAF?.length}</span></div>
                    <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2 w-3/4 rounded-sm'>Total Concession Applied<span className='ml-4 bg-yellow-400 text-white px-2 py-1 text-xs shadow-lg rounded-lg '>{countData?.Property?.concessions?.length}</span></div>
                    <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2 w-3/4 rounded-sm'>Total Trade Applied<span className='ml-4 bg-yellow-400 text-white px-2 py-1 text-xs shadow-lg rounded-lg '>{countData?.Trade?.totalApplications}</span></div>
                    <div className='bg-yellow-50 shadow-lg py-2 text-xs flex pl-4 items-center mt-2 w-3/4 rounded-sm'>Total Water Applied<span className='ml-4 bg-yellow-400 text-white px-2 py-1 text-xs shadow-lg rounded-lg '>{countData?.Water?.totalApplications}</span></div>
                </>}
            </div>
        </div>
    )
}

export default NotificationCard


